import React, { useState } from 'react';
import ingredients from './ingredients';

function RecipeBuilder() {
  const [selectedIngredients, setSelectedIngredients] = useState([]);
  const [showIngredientsList, setShowIngredientsList] = useState(false);

  const handleIngredientSelect = (ingredientName) => {
    // Add the ingredient to the selected list if not already added
    if (!selectedIngredients.includes(ingredientName)) {
      setSelectedIngredients([...selectedIngredients, ingredientName]);
    }
  };

  return (
    <div>
      <h2>Recipe Builder</h2>

      <button onClick={() => setShowIngredientsList(true)}>Choose Ingredients</button>

      {showIngredientsList && (
        <div>
          <h3>Available Ingredients:</h3>
          <ul>
            {Object.entries(ingredients).map(([name, details]) => (
              <li key={name} onClick={() => handleIngredientSelect(name)}>
                <strong>{name}</strong> - <small>{details.description}</small>
              </li>
            ))}
          </ul>
        </div>
      )}

      <h3>Selected Ingredients:</h3>
      <ul>
        {selectedIngredients.map(name => (
          <li key={name}>{name}</li>
        ))}
      </ul>

      {/* Future content for proportions goes here */}
    </div>
  );
}

export default RecipeBuilder;
